<template lang="">
    <div class="personal-con">
         <custHeader :title="title"></custHeader>
        <div class="personal-con-header">
            <div class="personal-con-header-top">
              <div  class="headPic" style="width:40px;height:40px; font-size:32px;display: inline-block;">
                <!-- <img src="" class="headPic" width="40px" height="40px;"></img> -->
                <i slot="icon" class="fa fa-user" ></i>
              </div>
              
              <span style="font-size: 12px;">{{ bcustCode }}</span>
              <span style="font-size: 12px;">{{ custLvl }}</span>
              <span style="font-size: 12px;">{{ riskLevel }}</span>
              <span style="font-size: 12px;">{{ custCls }}</span>
            </div>
              <div class="personal-con-header-body" style="display: flex;">
                <div class="" style="width:90%">
                  <div class="bodyDiv" style="    margin-top: 20px;">
                    <span class="span-first">总资产:</span>
                    <span class="span-second">{{ allFund  }}</span>
                    <span class="span-first">可用余额:</span>
                    <span class="span-second">{{ lastMoney }}</span>
                  </div>
                  <!-- <div class="bodyDiv">
                    <span style="    display: inline-block;width: 100px;">资金余额:</span>
                    <span>{{ lastMoney }}</span>
                  </div> -->
                  <div class="bodyDiv">
                    <span class="span-first">总市值:</span>
                    <span class="span-second">{{ marketValue }}</span>
                    <span class="span-first">购物资金:</span>
                    <span class="span-second">{{ shopMoney }}</span>
                  </div>


                  <div class="bodyDiv" style="    margin-bottom: 20px;">
                    <span class="span-first">在途资金:</span>
                    <span class="span-second">{{ fundUncomeAmt  }}</span>
                    <span class="span-first">中期冻结:</span>
                    <span class="span-second">{{ fundMidFrz }}</span>
                  </div>
                </div>
                <div class="more" >
                  <div class="height50" @click="toStar">
                        <i style=" font-size: 20px;color: #ddd;" class="fa " :class="faeye"></i>
                  </div>
                  <div class="height50" @click="getMore" style="font-size:12px;">
                    更多>
                  </div>

                </div>


                <!-- <div class="bodyDiv">

                </div> -->
              </div>
        </div>
        <div class="normal-box quick-menu">
          <!-- <div v-for="(item,index) in quickMenu"><router-link :to="item.path" >{{item.name}}</router-link></div> -->
          <mt-button type="" class="charge" @click="charge" name="button" >充值</mt-button>
          <mt-button type="" class="takeMoney" @click="takeMoney" name="button" >提现</mt-button>
        </div>
        <div class="menu-list">

               <mt-cell title="账户中心"  is-link  to="/personal/userInfo">
                <i slot="icon" class="fa fa-user" ></i>
               </mt-cell>
               <mt-cell title="我要交易" is-link  to="/trade">
                    <i slot="icon" class="fa fa-exchange" ></i>
               </mt-cell>
              <mt-cell title="我要查询"  is-link  to="/personal/fundLogQuery">
                  <i slot="icon" class="fa fa-search" ></i>
              </mt-cell>
              <mt-cell title="银行卡管理" :value="cardNum" is-link to="/personal/bankCards">
                       <i slot="icon" class="fa fa-id-card" ></i>
              </mt-cell>
              <mt-cell title="收货地址管理" :value="addressNum" is-link to="/personal/address">
                       <i slot="icon" class="fa fa-map-marker" ></i>
              </mt-cell>
              <!-- <mt-cell title="我的消息" :value="myMessage" is-link  to="/trade/search">
               <img slot="icon" :src="smrz">
              </mt-cell> -->
        </div>
       <div style="padding:12px 0;"> <mt-button  type="danger" size="large"  style="" @click="loginOut">退出</mt-button></div>


       <!--  <footer class="app-footer _line-fine"  >
           <div class="_effect ">
           <indexNav></indexNav>
           </div>
         </footer> -->
    </div>

</template>
<script>
import indexNav from 'components/index-nav.vue'
import cookieUtil from 'js-cookie'
import {MessageBox,Cell} from 'mint-ui'
import custHeader from  'components/custHeaderNoBack'
import src1 from "assets/l1.png"
import src2 from "assets/l2.png"
Vue.component(Cell.name,Cell);
export default{
    data(){
        return {
          title:"账户中心",
          faeye:"fa-eye",
            smrz:src1,
            fxpc:src2,
            authStatus:"",
            bcustCode:'',
            riskLevel:'',
            custCls:'',
            custLvl:'',
            allFund:"",
            lastMoney:"",
            marketValue:"",
            shopMoney:"",
            fundUncomeAmt:'',
            fundMidFrz:'',
            myMessage:'1',
            cardNum:'',
            addressNum:'',
            isShow:false
            }
    },
    created:function(){
         this.getRealnameAuthQry();
         this.qryCustBaseInfo();
         this.initData();
    },
    components: {
          indexNav,
          custHeader
      },
    methods:{
      getMore:function(){
        router.push("/info/fundDetail")
      },
      charge:function(){
        router.push("/charge")
      },
      takeMoney:function(){
        router.push("/takeMoney")
      },
      initData:function(){
        var self = this;
        axiosUtil.qryCustStatusInfo().then(data=>{
          var data=data.data.data;
          self.bcustCode = data.bcustCode
          self.riskLevel = data.riskLevel
          self.custCls = data.custCls
          self.custLvl = data.custLvl
        });

        axiosUtil.upCltFundAppQry().then(function(data){
          var data = data.data.data
          self.cardNum = data.length;
        })
        axiosUtil.upCltShopAddressQry().then(data => {
          var data = data.data;
          self.addressNum = data.data.length;
        });


        axiosUtil.qryCustSumAsset().then(data => {
          var data = data.data;
          if(data.errorcode==0){
              var data=data.data;
              self.allFund = data.mktVal*1 + data.fundBln*1 + ".00"
              self.lastMoney = data.fundAvl + ".00"
              self.marketValue = data.mktVal + ".00"
              self.shopMoney = data.fundShopAmt + ".00"
              self.fundUncomeAmt = data.fundUncomeAmt + ".00"
              self.fundMidFrz = data.fundMidFrz + ".00"
          }else{
              MessageBox("提示",data.errormsg);
          }
        })
      },
      qryCustBaseInfo:function(){

      },
       getRealnameAuthQry:function(){
           axiosUtil.getRealnameAuthQry().then(data=>{
            var data=data.data;
           // 0;//0未//1已//2未通过//4审核中
            if(data.errorcode==0){
                if(data.data.authStatus==0){
                    this.authStatus="未认证";
                }else if(data.data.authStatus==1){
                   this.authStatus.authStatus="已认证";
                }else if(data.data.authStatus==2){
                   this.authStatus="未通过";
                }else if(data.data.authStatus==4){
                   this.authStatus="审核中";
                }
            }else{
              this.authStatus="未认证";
            }
          })
       },
       loginOut:function(){
        axiosUtil.upCltLogout().then(data=>{
          var data=data.data;
          if(data.errorcode==0){
            this.$router.push("/");
          }
        })
       },
       toStar:function() {
        if(!this.isShow){
          this.faeye="fa-eye-slash";
          this.isShow = true;
          this.allFund = "****"
          this.lastMoney = "****"
          this.marketValue = "****"
          this.shopMoney = "****"
          this.fundUncomeAmt = "****"
          this.fundMidFrz = "****"
        }else{
          this.initData();
          this.faeye="fa-eye";
          this.isShow = false
        }
       }
    }
}

</script>
<style scoped >
.headPic{
  margin-top: 6px;
  margin-left: 25px;
}
.height50{
  height: 50px;
line-height: 50px;
}
.bodyDiv{
  text-align: left;
  height: 36px;
  font-size: 3vmin;
  line-height: 36px;
  margin-left: 3%;
}
.personal-con{
 /*   position: fixed;*/
   height: 100%;
    width: 100%;
    top: 0;
    background: #f1f1f1;
    z-index: 4;
    overflow: auto;
}
.personal-con-header {
    text-align: center;
    /*padding: 36px;*/
    background: #FF4545;
    color: #fff;
    font-size: 22px;
  /*  position: absolute;*/
    width: 100%;
    top: 0;
    z-index: 5
}
.personal-con-header-top{
  height: 54px;
  line-height: 54px;
  border-bottom: 1px solid #fff;
  text-align: left;
}
/*.personal-con-header img{
    width: 80px;
    height: 80px;
}
*/
.more{
  width: 10%;
  height: 108px;
  line-height: 108px;
  padding-top: 20px;
}
.span-first{
  display: inline-block;
  width: 16vmin;
  text-align: right;
}
.span-second{
  display: inline-block;
  width: 21vmin;
  text-align: right;
}
.set{
  background-color: transparent;
  float: left;
  margin-left: 10px;
  margin-top: 2px;
  width: 33px;
}
.set span{
  display: block;
  width: 33px;
  height: 3px;
  background: #fff;
  margin-top: 5px;
}
.allMake{
  float: right;
  background-color: rgba(255,255,255,0.3);
  margin-right: 20px;
  margin-top: 6px;
}
.personal-con-header-top button p{
  color: #fff;
}

.allPosition{
  font-size: 13px;
  float: left;
  width: 55px;
  display: block;
  margin-left: 7px;
}
.allMoney{
  font-size: 13px;
  float: right;
  width: 55px;
  display: block;
  margin-right: 7px;
}

.charge,.takeMoney{
  display: block;
  height: 27px;
  margin-left: 5%;
  width: 43%;
  margin-right: 5%;
  background: #26ADED;
  color:#fff;
   -webkit-box-flex: 1; -ms-flex: 1;flex: 1;
   flex-grow: 1;
}
.takeMoney{
 background: #FF9B45;
}



.menu-list >a{
    margin-top: 12px;
}
.quick-menu{
  background: #fff;
  color: #999;
  border-top: 1px solid #f1f1f1;
  padding: 12px 0;
  /*margin-top: 210px;*/
}
.quick-menu a{
  text-decoration: none;
  color: #333;
}
.app-footer{
  position: fixed;
}
</style>



